body {
    color: white;
    font-family: 'Comfortaa', cursive;
    scroll-behavior: smooth;
}
.Emotion {
    background-color: black;
    height: 100vh;
    width: 100vw;
    text-align: center;
    display: table-cell;
    vertical-align: middle;
    position: relative;
    overflow-y: hidden;
}
#emotion {
    width: 50vw;
    position: relative;
    border-radius: 3%;
    opacity: 0.85;
}

#button {
    display: inline;
    position: relative;
    border-radius: 5%;
    border: white 2px solid;
    color :white;
    background-color: black;
    margin: auto;
    font-family: 'Comfortaa', cursive;
    font-size: 110%;
    opacity: 0.9;
    -webkit-transition:all .5s ease-in-out;
    -o-transition:all .5s ease-in-out;
    transition:all .5s ease-in-out;
}
#button::after {
    content: "";
    border-radius: 5%;
    position: relative;
    z-index: -1;
    opacity: 0;
    -webkit-box-shadow: 0 0px 0px white;
            box-shadow: 0 0px 0px white;
    font-weight: bold;
    -webkit-transition:all .5s ease-in-out;
    -o-transition:all .5s ease-in-out;
    transition:all .5s ease-in-out;
}
#button:hover {
    -webkit-transform: scale(1.10,1.10);
    -ms-transform: scale(1.10, 1.10);
        transform: scale(1.10, 1.10);
}
#button:hover::after {
    opacity: 1;
}
#wrap {
    position: relative;
    display: inline-block;
    margin: 0 auto;
}
#words {
    display: inline-block;
    position: relative;
    margin: 5px;
}
